import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Class1Provider } from '../../providers/class1/class1';


@Component({
  selector: 'page-home',
  templateUrl: 'about.html'
})
export class AboutPage {

  classes: any;
  private db: any;
  model: any = {};
  
  constructor(public navCtrl: NavController, public classService: Class1Provider) {
    this.classService.loadClasses();
  }

  itemTapped(clz){
    this.classService.itemTapped(clz);
  }

  doRefresh(refresher){
    this.classService.doRefresh(refresher);
  }

  loadClasses(){
    this.classService.loadClasses();
  }
  
}





